<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>通知管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/info' }">通知公告</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input ref="" v-model="queryInfo.userName" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getInfoList">
            <el-button slot="append" icon="el-icon-search" @click="getInfoList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加公告</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :data="InfoList"
          :header-cell-style="headClass"
          border
          style="width: 100%">
        <el-table-column
            label="ID"
            prop="id"
            width="50px">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="userName"
            width="100px">
        </el-table-column>
        <el-table-column
            label="标题"
            prop="title">
        </el-table-column>
        <el-table-column
            label="内容"
            :show-overflow-tooltip="true"
            prop="content"
        >
        </el-table-column>
        <el-table-column
            label="发布时间"
            prop="createTime"
            width="270px">
        </el-table-column>
        <el-table-column
            label="操作"
            width="200px"
        >
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary" @click="editInfo(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteInfo(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="queryInfo.pageNum"
          :page-size="queryInfo.pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!--新增Info对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="编辑通知"
        width="35%"
    >
      <!--这是内容区域-->
      <el-form ref="editInfoFormRef" :model="Info" class="demo-Info" label-width="100px">
        <el-form-item label="用户名" prop="userName">
          <el-input v-model="Info.userName"></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="Info.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input v-model="Info.content"></el-input>
        </el-form-item>

        <el-form-item label="发布时间" prop="createTime">
          <el-date-picker
              style="float:left"
              v-model="Info.createTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="addInfo">确 定</el-button>
    </el-dialog>
    <!--修改Info对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="编辑通知"
        width="35%"
        @close="closeEditDialog">
      <!--这是内容区域-->
      <el-form ref="editInfoFormRef" :model="Info" class="demo-Info" label-width="100px">
        <el-form-item label="用户名" prop="userName">
          <el-input v-model="Info.userName" disabled></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="Info.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input v-model="Info.content"></el-input>
        </el-form-item>
        <el-form-item label="发布时间" prop="createTime">
          <el-date-picker
              style="float:left"
              v-model="Info.createTime"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateInfo">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Info",
  data() {
    return {
      queryInfo: {
        pageNum: 1,
        pageSize: 5,
        userName: '',
      },
      InfoList: [],
      total: 0,
      Info: {
        createTime: ''
      },
      editVisible: false,
      addVisible: false,
    }
  },
  methods: {
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    getInfoList() {
      if (this.queryInfo.userName === '') {
        this.$axios.post('/info/queryInfo', this.queryInfo).then(res => {
          if (res.data.status === 10000) {
            this.InfoList = res.data.data.list;
            this.total = res.data.data.total;
            this.$message.success("请求成功！");
          } else {
            this.$message.error("请求失败！");
          }
        })
      } else {
        this.$axios.post('/info/queryInfoByName', this.queryInfo).then(res => {
          console.log(res)
          this.total = res.data.data.total;
          this.InfoList = res.data.data.list;
        })
      }
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getInfoList();
    },
    handleCurrentChange(newNum) {
      this.queryInfo.pageNum = newNum;
      this.getInfoList();
    },
    editInfo(res) {
      this.Info = res;
      this.editVisible = !this.editVisible;
      console.log(this.Info)
    },
    closeEditDialog() {
      //重置表单
      this.$refs["editInfoFormRef"].resetFields();
    },
    deleteInfo(row) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '删除用户', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // console.log(row.id)
        // 点击确定，发起删除
        this.$axios.get('/admin/Info/delete/' + row.id).then(res => {
          // console.log(row.id);
          this.getInfoList();
          this.$message.success("删除用户成功！")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    updateInfo() {
      this.$axios.post('/admin/Info/update', this.Info).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("更新成功！")
          this.editVisible = !this.editVisible;
          this.getInfoList();
        } else {
          this.$message.error("更新失败！")
        }
      })
    },
    addInfo() {
      this.$axios.post('/admin/Info/addInfo', this.Info).then(res => {
        if (res.data.status === 10000) {
          this.addVisible = !this.addVisible;
          this.$message.success("新增成功！")
          this.getInfoList();
        } else {
          this.addVisible = !this.addVisible;
          this.$message.error("新增失败！")
        }
      })
    }
  },
  created() {
    this.getInfoList();
  }
}
</script>

<style>

.el-tooltip__popper {
  max-width: 20%;
}

.el-tooltip__popper, .el-tooltip__popper.is-dark {
  background: rgb(48, 65, 86) !important;
  color: #fff !important;
  line-height: 24px;
}
</style>
